.Page {
	margin-top: -21px;
}

.Page__header--bg {
	width: 100%;
	height: 300px;
	background: url('../../images/foods-bg.jpg') no-repeat;
}

.Page__header {
	height: 100%;
	.SearchContainer {
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		.form-group {
			width: 40%;
			text-align: center;
			label {
				color: white;
				font-size: 24px;
				text-transform: uppercase;
				margin-bottom: 20px;
				text-shadow: 1px 1px 3px #333;
			}
		}
	}	
}

.Page__title {
	text-align: center;
	margin: 30px 0px;
}